
Template.__checkName("atSocial");
Template["atSocial"] = new Template("Template.atSocial", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, "\n    ", HTML.BUTTON({
    "class": function() {
      return [ "at-social-btn ", Spacebars.mustache(view.lookup("disabled")), " waves-effect waves-light btn" ];
    },
    id: function() {
      return [ "at-", Spacebars.mustache(view.lookup("name")) ];
    },
    name: function() {
      return Spacebars.mustache(view.lookup("name"));
    }
  }, "\n      ", HTML.I({
    "class": function() {
      return [ Spacebars.mustache(view.lookup("iconClass")), " left" ];
    }
  }), " ", Blaze.View("lookup:buttonText", function() {
    return Spacebars.mustache(view.lookup("buttonText"));
  }), "\n    "), "\n  ");
}));
