
Template.__checkName("atSep");
Template["atSep"] = new Template("Template.atSep", (function() {
  var view = this;
  return HTML.DIV({
    "class": "at-sep row center-align"
  }, "\n    ", HTML.STRONG(Blaze.View("lookup:sepText", function() {
    return Spacebars.mustache(view.lookup("sepText"));
  })), "\n  ");
}));
